const getContent = (param: string | null): VariantContent => {
  if (!param) return VARIANT_CONTENT.default
  return VARIANT_CONTENT[param as keyof typeof VARIANT_CONTENT] || VARIANT_CONTENT.default
}

const ImageContainer = ({ children }: { children: React.ReactNode }) => (
  <Box
    sx={{
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      bgcolor: "transparent",
      borderRadius: 4,
      overflow: "hidden",
      minWidth: 460,
      minHeight: 341,
    }}
  >
    {children}
  </Box>
)

const Loading = () => (
  <Box
    sx={{
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      bgcolor: "transparent",
    }}
  >
    <Box sx={{ width: 24, height: 24, bgcolor: "transparent", borderRadius: "4px" }} />
  </Box>
)

const getEnvWatchlistId = (env: string, content: VariantContent) => {
  switch (env) {
    case "internal":
    case "development":
      return content.dev
    case "staging":
      return content.stg
    case "production":
      return content.prod
    default:
      return content.prod
  }
}

const InfluencerLoginSection = () => {
  const [searchParams] = useSearchParams()
  const param = searchParams.get("utm_content")
  const content = getContent(param)

  if (content && param) {
    sessionStorage.setItem("watchlistId", getEnvWatchlistId(environment.name, content))
  }

  return (
    <Box
      sx={{
        height: "100%",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        mt: 2,
        px: 12,
      }}
    >
      <Stack
        sx={{
          height: "100%",
          display: "flex",
          alignItems: "flex-start",
          justifyContent: "flex-start",
          pt: 2,
        }}
      >
        {content.heading}
        <ImageContainer>
          <LazyImage
            src={content.imageUrl}
            alt="Login"
            width={IMAGE_DIMENSIONS.width}
            loadingFallback={<Loading />}
            fallback={<Loading />}
          />
        </ImageContainer>
        <Typography mt={1} fontSize={16} width={430}>
          {content.description}
        </Typography>
      </Stack>
    </Box>
  )
}

export { InfluencerLoginSection }
